.app-wrapper {
    background-color: #EDF2F6;
}
.main-container {
    display: flex;
    justify-content: space-between;
}
.app-wrapper-content {
    padding: 20px;
    min-height: 100vh;
    margin-top: 20px;
    width: 70%;
    background-color: #ffffff;
}


.container {
    padding-left: 15px;
    padding-right: 15px;
    margin: 0 auto;
}

@media (min-width: 360px) {
    .container {
        padding-left: 10px;
        padding-right: 10px;
        width: 100%;
        box-sizing: border-box;
    }
}

@media (min-width: 768px) {
    .container {
        width: 738px;
    }
}

@media (min-width: 1024px) {
    .container {
        width: 994px;
    }
}

@media (min-width: 1200px) {
    .container {
        width: 1170px;
    }
}
